import Vue from 'vue'
import Router from 'vue-router'

import loading from '@/components/loading'

import repair from '@/pages/repair/index'     //
import chooseRepair from '@/pages/repair/chooseRepair'  //
import submitSuccess from '@/pages/repair/success'  //

import zhifu from '@/pages/zhifu/index'     //

//
import myRepair from '@/pages/myRepair/myrepair' //
import orderdetail from '@/pages/myRepair/orderdetail' //
import cancel from '@/pages/myRepair/cancel'  //

Vue.use(Router)

const router= new Router({
  routes: [
    {
      path:'/repair',
      name:'repair',
      component:repair,
      meta:{
        title:''
      }
    },
    {
      path:'submitSuccess',
      name:'submitSuccess',
      component:submitSuccess,
      meta:{
        title:''
      }
    },
    {
      path:'/chooseRepair',
      name:'chooseRepair',
      component:chooseRepair
    },
    {
      path:'/zhifu',
      name:'zhifu',
      component:zhifu,
      meta:{
        title:''
      }
    },
    {
      path: '/myRepair',
      name: 'myRepair',
      component:myRepair,
    },
    {
      path: '/orderdetail',
      name: 'orderdetail',
      component: orderdetail,
    },{
      path:'/cancel',
      name:'cancel',
      component:cancel,
    }
  ]
});
//
// to  from next() 
router.beforeEach((to, from, next) => {
    //title
    changeTitle(to.meta.title)
    next();
});

//title
function changeTitle(title) {
    title = title ? title : '';
    window.document.title = title;
};

export default router



